.form-wrapper {
	width: 100%;
	border-radius: 4px;
	background-color: #f0f0f0;
	padding: 8px 10px 18px;
	margin-bottom: 20px;
}
.form-wrapper,
.form-wrapper *,
.form-wrapper *:before,
.form-wrapper *:after {
	box-sizing: border-box;
}
.form-wrapper > h3 {
	font-size: 16px;
}
.form-elem,
.form-elem label {
	width: 100%;
	max-width: 550px;
}
.form-elem + .form-elem {
	margin-top: 8px;
}
.label-text {
	display: inline-block;
	width: 20%;
	font-size: 14px;
}
.input-text {
	display: inline-block;
	width: 65%;
	max-width: 200px;
	padding: 7px;
	border-radius: 4px;
	border: 1px solid #ccc;
	transition: border-color 0.2s ease-in-out;
}
.input-text:focus {
	border-color: #333;
	outline: none;
}